import { useEffect } from "react";
import TimekitBooking from "timekit-booking";
import { useSelector } from "react-redux";
import { State } from "@/redux/Types";
import environment from "../constants/Environment";

interface Props {
  onClose: () => void;
}

const environmentData = environment();

export default function TimeKitComponent({ onClose }: Props) {
  const userName = useSelector((state: State) => state.user.userName);
  const userEmail = useSelector((state: State) => state.user.userEmail);

  useEffect(() => {
    const widget = new TimekitBooking();
    widget.init({
      app_key: environmentData.TIMEKIT_API_KEY,
      project_id: environmentData.TIMEKIT_PROJECT_ID,
      customer_fields: {
        name: {
          title: "Full name",
          prefilled: userName,
          readonly: false,
        },
        email: {
          title: "E-mail",
          prefilled: userEmail,
          readonly: false,
        },
      },
    });

    return () => {
      const element = document.getElementById("bookingjs");
      element?.remove();
    };
  }, []);

  return (
    <div className="overlay">
      <div
        style={{
          width: "600px",
          margin: "0 auto",
          position: "absolute",
          zIndex: "1000",
          top: "50%",
          left: "50%",
          transform: "translate(-50%, -50%)",
        }}
      >
        <div style={{ display: "flex", justifyContent: "flex-end" }}>
          {/* <h1>Time Kit component</h1> */}
          <span className="closetime-kit" onClick={onClose}>
            <svg
              width="13"
              height="13"
              viewBox="0 0 10 10"
              fill="#C1C1C1"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M10 1.00714L8.99286 0L5 3.99286L1.00714 0L0 1.00714L3.99286 5L0 8.99286L1.00714 10L5 6.00714L8.99286 10L10 8.99286L6.00714 5L10 1.00714Z"
                fill="#C1C1C1"
              ></path>
            </svg>
          </span>
        </div>
        <div id="bookingjs"></div>
      </div>
    </div>
  );
}
